import React from "react";
import {useNavigate} from "react-router-dom";
import {ProjectData, ProjectTag, searchProjects, searchTags} from "../data/ProjectData";

/** Data stored globally */
type RadiantContextProps = {
  /** True when navigation drawer is open */
  drawerOpen: boolean;
  /** Opens or closes the navigation drawer */
  setDrawerOpen: (open: boolean) => void;
  /** Redirects to the given url, prevents default for the event given */
  navigate: (route: string, event?: React.UIEvent) => void;

  /** The current search string */
  search: string;
  /** Sets the search to the given value */
  setSearch: (value: string) => void;
  /** True when search suggestions are visible */
  suggestionsOpen: boolean;
  /** Changes whether search suggestions are visible */
  setSuggestionsOpen: (open: boolean) => void;
  /** The list of projects that match the search */
  foundProjects: ProjectData[];
  /** The list of tags that match the search */
  foundTags: ProjectTag[];
};

/** Contains global data and allows listening to updates */
export const RadiantContext = React.createContext<RadiantContextProps>({} as RadiantContextProps);

/** Allows to pull variables from the context */
export const useRadiantContext = () => React.useContext(RadiantContext);

/** Initializes the global data values */
export const RadiantContextProvider = (props: {children: React.ReactNode}) => {
  const [drawerOpen, setDrawerOpen] = React.useState(false);
  const navigateReact = useNavigate();
  const navigate = (route: string, event?: React.UIEvent) => {
    navigateReact(route);
    setDrawerOpen(false);
    if (event) event.preventDefault();
  };

  const [suggestionsOpen, setSuggestionsOpen] = React.useState(false);
  const [search, setSearch] = React.useState("");
  const foundProjects = React.useMemo(() => searchProjects(search), [search]);
  const foundTags = React.useMemo(() => searchTags(search), [search]);

  return <RadiantContext.Provider value={{
    drawerOpen, setDrawerOpen,
    navigate,
    suggestionsOpen, setSuggestionsOpen,
    search, setSearch,
    foundProjects,
    foundTags,
  }}>
    {props.children}
  </RadiantContext.Provider>;
};
