import React from "react";
import {Box, useMediaQuery, useTheme} from "@mui/material";

/** The width at which the app will switch to mobile display */
export const ConfigMobileWidth = 760;
/** The width at which the app will switch to mobile display */
export const ConfigSmallscreenWidth = 1280 + 240;
/** The width at which the app will switch to wide display */
export const ConfigWidescreenWidth = 1920;
/** Size of the content boxes */
export const ConfigContentWidth = 1920;
/** Size of the content within panels */
export const ConfigPanelWidth = 1024;
/** The width of the project card */
export const ConfigProjectCardWidth = 500;
/** The height of the project card */
export const ConfigProjectCardHeight = 500;
/** Text shadow to contrast with the background images */
export const ConfigTextShadow = "0 0 1px transparent,0 2px 3px rgba(0,0,0,.8)";
/** The maximum width of the search bar */
export const ConfigSearchMaxWidth = 500;

/** Returns true when viewing on mobile device */
export const useMobile = () => {
  return useMediaQuery(`(max-width: ${ConfigMobileWidth}px)`);
};

/** Returns true when viewing on tablet device */
export const useSmallScreen = () => {
  return useMediaQuery(`(max-width: ${ConfigSmallscreenWidth}px)`);
};

/** Returns true when viewing on wide screen device */
export const useWideScreen = () => {
  return useMediaQuery(`(min-width: ${ConfigWidescreenWidth}px)`);
};

/** Displays the children only if viewing on mobile device */
export const MobileOnly = (props: {children: React.ReactNode}) => {
  const isMobile = useMobile();
  return <>{isMobile ? props.children : null}</>;
};

/** Displays the children only if viewing on desktop device */
export const DesktopOnly = (props: {children: React.ReactNode}) => {
  const isMobile = useMobile();
  return <>{isMobile ? null : props.children}</>;
};

/** Displays given children only on narrow screens (mobile and tablet sizes) */
export const SmallScreenOnly = (props: {children: React.ReactNode}) => {
  const isSmall = useSmallScreen();
  return <>{isSmall ? props.children : null}</>;
};

/** Displays given children only on wide screens (desktop size) */
export const LargeScreenOnly = (props: {children: React.ReactNode}) => {
  const isSmall = useSmallScreen();
  return <>{isSmall ? null : props.children}</>;
};

/** Places a linebreak only for desktop */
export const DesktopBr = () => {
  const isMobile = useMobile();
  return <>{isMobile ? null : <br />}</>;
};

/** Inserts a vertical space of the given theme size */
export const Spacer = (props: {size: number}) => {
  const theme = useTheme();
  return <Box sx={{minHeight: theme.spacing(props.size)}}></Box>;
};

/** Inserts a box that fills all of the leftover flex space */
export const Filler = () => {
  return <Box sx={{flexGrow: 1}} />;
};
