import {Box, Button, Stack, Typography} from "@mui/material";
import React from "react";
import {ConfigPanelWidth, ConfigTextShadow, Spacer, useMobile} from "./Extensions";
import {useRadiantContext} from "./RadiantContext";

interface PanelContentProps {
  flavor: string,
  title: string,
  description: React.ReactNode,
  align: "right" | "left" | "center",
  primaryButtonTitle?: string,
  primaryButtonUrl?: string,
  secondaryButtonTitle?: string,
  secondaryButtonUrl?: string,
}

/** A centered box with optional background that keeps content an expected width */
export const PanelContent: React.FC<PanelContentProps> = ({align, flavor, title, description, primaryButtonTitle, primaryButtonUrl, secondaryButtonTitle, secondaryButtonUrl}) => {
  const isMobile = useMobile();
  const context = useRadiantContext();
  return (
    <Box sx={{display: "flex", justifyContent: align, maxWidth: ConfigPanelWidth, width: ConfigPanelWidth}}>
      <Stack sx={{
        textShadow: ConfigTextShadow,
        maxWidth: 768,
      }} spacing={1}>
        <Typography align={isMobile ? "center" : align} variant="h3" textTransform="uppercase">{flavor}</Typography>
        <Typography align={isMobile ? "center" : align} variant="h1" textTransform="uppercase">{title}</Typography>
        <Typography align={isMobile ? "center" : align}>{description}</Typography>
        <Spacer size={1} />
        <Stack spacing={1} direction="row" justifyContent={isMobile ? "center" : align}>
          {primaryButtonTitle && <Button variant="contained" size="large" onClick={() => primaryButtonUrl && context.navigate(primaryButtonUrl)}>{primaryButtonTitle}</Button>}
          {secondaryButtonTitle && <Button variant="contained" size="large" onClick={() => secondaryButtonUrl && context.navigate(secondaryButtonUrl)}>{secondaryButtonTitle}</Button>}
        </Stack>
      </Stack>
    </Box>
  );
};
