import {alpha, Chip, Typography, useTheme} from "@mui/material";
import React from "react";
import {findTag} from "../data/ProjectData";
import {ROUTES} from "../RadiantRouter";
import {useRadiantContext} from "./RadiantContext";

/** A project tag that leads to tag search on click */
export const ProjectTag = (props: {tag: string, color: "default" | "primary"}) => {
  const theme = useTheme();
  const tag = findTag(props.tag);
  const context = useRadiantContext();

  const icon = <Typography sx={{
    backgroundColor: alpha("#151d28", 0.35),
    borderRadius: 16,
    fontSize: "0.8125rem",
    paddingX: theme.spacing(1),
    paddingY: theme.spacing(0.1),
  }}>{tag.count}</Typography>;

  const color = React.useMemo(() => {
    switch (props.tag) {
      case "difficulty-easy":
        return "#468232";
      case "difficulty-medium":
        return "#be772b";
      case "difficulty-hard":
        return "#a53030";
      default:
        return "#3c5e8b";
    }
  }, [props]);

  const link = React.useMemo(() => ROUTES.Library + "?tag" + props.tag, [props]);

  return (
    <Chip
      clickable
      href={link}
      onClick={event => {
        context.setSuggestionsOpen(false);
        context.navigate(link + "foo", event);
      }}
      component="a"

      icon={icon}
      color={props.color}
      label={`#${tag.name}`}
      sx={{
        boxShadow: theme.shadows[2],
        backgroundColor: color,
        "&:hover": {backgroundColor: alpha("#ebede9", 0.25)},
      }}
    />
  );
};
