import {Link, Outlet} from "react-router-dom";
import {alpha, AppBar, Box, Button, Divider, Drawer, IconButton, InputAdornment, inputAdornmentClasses, List, ListItem, ListItemButton, ListItemIcon, ListItemText, outlinedInputClasses, Stack, TextField, Toolbar, Typography, useTheme} from "@mui/material";
import React from "react";
import {ConfigSearchMaxWidth, DesktopOnly, Filler, LargeScreenOnly, SmallScreenOnly, useMobile} from "./Extensions";
import MenuIcon from "@mui/icons-material/Menu";
import {NavigationConfig, ROUTES} from "../RadiantRouter";
import {Logo} from "./Logo";
import {RadiantContextProvider, useRadiantContext} from "./RadiantContext";
import SearchIcon from "@mui/icons-material/Search";
import {useDebounce} from "use-debounce";
import {ProjectTag} from "./ProjectTag";
import {ProjectCardite, SummaryProjectCardite} from "./ProjectCardite";
import {ContentBox} from "./ContentBox";
import YouTubeIcon from "@mui/icons-material/YouTube";
import VolunteerActivismIcon from "@mui/icons-material/VolunteerActivism";
import SportsEsportsIcon from "@mui/icons-material/SportsEsports";

/** Navigation buttons at the top of the header for the desktop devices */
const WideScreenNavigation = () => {
  const theme = useTheme();
  const buttons = NavigationConfig.map(({title, route, icon, highlight}, index) => (
    <Button key={index} component={Link} to={route} variant={highlight ? "white" : "transparent"} color="inherit" sx={{paddingX: theme.spacing(2), marginLeft: highlight ? theme.spacing(1) : 0, whiteSpace: "nowrap"}}>
      {title}
    </Button>
  ));
  return (
    <LargeScreenOnly>
      <Stack direction="row" spacing={0}>
        {buttons}
      </Stack>
    </LargeScreenOnly>
  );
};

/** Burger navigation button for the mobile that simply opens the drawer */
const NarrowScreenNavigation = () => {
  const context = useRadiantContext();
  return (
    <SmallScreenOnly>
      <IconButton edge="start" color="inherit" onClick={() => context.setDrawerOpen(true)}>
        <MenuIcon />
      </IconButton>
    </SmallScreenOnly>
  );
};

/** Drawer with navigation for mobile devices */
const NarrowScreenDrawer = () => {
  const theme = useTheme();
  const context = useRadiantContext();
  const buttons = NavigationConfig.map(({title, route, icon, highlight}, index) => (
    <ListItem key={index} disablePadding>
      <ListItemButton onClick={() => context.navigate(route)}>
        <ListItemIcon sx={{color: theme.palette.text.primary}}>{icon}</ListItemIcon>
        <ListItemText primary={title} />
      </ListItemButton>
    </ListItem>
  ));
  return (
    <SmallScreenOnly>
      <Drawer anchor="right" variant="temporary" open={context.drawerOpen} onClose={() => context.setDrawerOpen(false)}>
        <Stack direction="column" spacing={1} sx={{flexGrow: 1, backgroundColor: theme.palette.background.default, padding: theme.spacing(2)}}>
          <Stack direction="row" spacing={2} paddingX={2} paddingY={1}>
            <Logo sx={{height: 32, width: 32}} />
            <Typography variant="h6">OCWALK</Typography>
          </Stack>
          <Divider />
          <List>{buttons}</List>
        </Stack>
      </Drawer>
    </SmallScreenOnly>
  );
};

/** Search field for website content at the top of the page */
const SearchField = () => {
  const [searchValue, setSearchValue] = React.useState("");
  const theme = useTheme();
  const isMobile = useMobile();
  const context = useRadiantContext();
  const [debouncedSearch] = useDebounce(searchValue, 100);
  React.useEffect(() => context.setSearch(debouncedSearch), [debouncedSearch]);
  const projectsSuggested = 4;
  const tagsSuggested = 3;
  React.useEffect(() => context.setSuggestionsOpen(searchValue.length > 0), [searchValue]);

  const searchField = (
    <TextField
      sx={{
        [`.${outlinedInputClasses.root}`]: {
          fontSize: "1rem",
          height: 44,
          borderRadius: "4px",
          backgroundColor: alpha(theme.palette.common.black, 0.25),
          "&:hover": {backgroundColor: alpha(theme.palette.common.black, 0.35)},
        },
        [`.${outlinedInputClasses.input}`]: {
          paddingLeft: theme.spacing(1),
        },
        [`.${outlinedInputClasses.notchedOutline}`]: {
          border: "0 !important",
        },
        [`.${inputAdornmentClasses.root}`]: {
          color: theme.palette.common.white,
          pointerEvents: "none",
        },
      }}
      value={searchValue}
      onChange={event => setSearchValue(event.target.value)}
      onFocus={event => searchValue.length > 0 && context.setSuggestionsOpen(true)}
      fullWidth
      placeholder="Search..."
      InputProps={{
        startAdornment: (
          <InputAdornment position="start">
            <SearchIcon />
          </InputAdornment>
        ),
      }}
    />
  );

  const suggestions = (
    <Stack spacing={1} sx={{
      position: "absolute",
      width: "100%",
      boxSizing: "content-box",
      backgroundColor: "#151d28",
      boxShadow: theme.shadows[5],
      display: context.suggestionsOpen ? "inherit" : "none",
    }}>
      {context.foundProjects.length > 0 && <Stack spacing={1}>
        {context.foundProjects.slice(0, projectsSuggested).map(project => <ProjectCardite projectId={project.id} />)}
      </Stack>}
      <SummaryProjectCardite extraCount={context.foundProjects.length - projectsSuggested} clearCode={() => setSearchValue("")} />
      {context.foundTags.length > 0 && <Stack direction="row" paddingX={1} overflow="hidden" spacing={1} paddingBottom={1}>
        {context.foundTags.slice(0, tagsSuggested).map(tag => <ProjectTag tag={tag.name} color="primary" />)}
      </Stack>}
    </Stack>
  );

  return (
    <Box sx={{flexGrow: 999, position: "relative", maxWidth: isMobile ? "inherit" : ConfigSearchMaxWidth}}>
      {searchField}
      {suggestions}
    </Box>
  );
};

/** Header layout */
const Header = () => {
  const theme = useTheme();
  const isMobile = useMobile();
  const context = useRadiantContext();
  return (
    <AppBar position="sticky">
      <Toolbar disableGutters={true} sx={{paddingX: theme.spacing(isMobile ? 1 : 4), maxWidth: "100%"}}>
        <Stack direction="row" spacing={isMobile ? 1 : 2} sx={{flexGrow: 1}}>
          <Button variant="transparent" color="inherit" onClick={() => context.navigate(ROUTES.Home)}>
            <Stack direction="row" spacing={1}>
              <Logo sx={{height: 32, width: 32}} />
              <DesktopOnly>
                <Typography variant="h6">OCWALK</Typography>
              </DesktopOnly>
            </Stack>
          </Button>
          <DesktopOnly><Filler /></DesktopOnly>
          <SearchField />
          <WideScreenNavigation />
          <NarrowScreenNavigation />
        </Stack>
        <NarrowScreenDrawer />
      </Toolbar>
    </AppBar>
  );
};

export const SocialFooter = () => {
  return (
    <ContentBox unlockHeight>
      <Stack spacing={2}>
        <Typography align="center">You can also find us here:</Typography>
        <Stack direction="row" spacing={2}>
          <Button variant="contained" component="a" href="https://www.youtube.com/@OcarinaWalk" startIcon={<YouTubeIcon />}>YouTube</Button>
          <Button variant="contained" component="a" href="https://www.patreon.com/wispy" startIcon={<VolunteerActivismIcon />}>Patreon</Button>
          <Button variant="contained" component="a" href="https://discord.com/invite/QX7C3YsSnF" startIcon={<SportsEsportsIcon />}>Discord</Button>
        </Stack>
      </Stack>
    </ContentBox>
  );
};

/** Layout of all pages where outlet is the actual body of the page */
export const CommonLayout = () => {
  return (
    <RadiantContextProvider>
      <Stack sx={{overflowX: "hidden"}}>
        <Header />
        <Outlet />
      </Stack>
    </RadiantContextProvider>
  );
};
