import {Route, Routes} from "react-router-dom";
import {CommonLayout} from "./components/CommonLayout";
import {HomePage} from "./pages/Home.page";
import {PageNotFoundPage} from "./pages/PageNotFound.page";
import HomeIcon from "@mui/icons-material/Home";
import QueueMusicIcon from "@mui/icons-material/QueueMusic";
import PlaylistAddIcon from "@mui/icons-material/PlaylistAdd";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import SportsEsportsIcon from "@mui/icons-material/SportsEsports";
import HelpOutlineIcon from "@mui/icons-material/HelpOutline";
import VolunteerActivismIcon from "@mui/icons-material/VolunteerActivism";
import React from "react";

/** Describes all of the routed pages of the app */
export const ROUTES = {
  Home: "/",
  Library: "/library",
  Commission: "/commission",
  BuyOcarina: "/shop",
  Discord: "/discord",
  About: "/about",
  Contact: "/contact",
  Patreon: "/patreon",
};

/** Describes all navigation of the app */
export const NavigationConfig = [
  {title: "Home", /*        */ route: ROUTES.Home, /*       */ icon: <HomeIcon />, /*               */ highlight: false},
  {title: "Library", /*     */ route: ROUTES.Library, /*    */ icon: <QueueMusicIcon />, /*         */ highlight: false},
  {title: "Commission", /*  */ route: ROUTES.Commission, /* */ icon: <PlaylistAddIcon />, /*        */ highlight: false},
  {title: "Buy Ocarina", /* */ route: ROUTES.BuyOcarina, /* */ icon: <ShoppingCartIcon />, /*       */ highlight: false},
  {title: "Discord", /*     */ route: ROUTES.Discord, /*    */ icon: <SportsEsportsIcon />, /*      */ highlight: false},
  {title: "About", /*       */ route: ROUTES.About, /*      */ icon: <HelpOutlineIcon />, /*        */ highlight: false},
  {title: "Patreon", /*     */ route: ROUTES.Patreon, /*    */ icon: <VolunteerActivismIcon />, /*  */ highlight: true},
];

/**
 * Decides which page to show based on current url
 */
export const RadiantRouter = () => (
  <Routes>
    <Route element={<CommonLayout />}>
      <Route path={ROUTES.Home} element={<HomePage />} />
      <Route path="*" element={<PageNotFoundPage />} />
    </Route>
  </Routes>
);
