import {alpha, Box, Button, ButtonBase, Card, Stack, Typography, useTheme} from "@mui/material";
import React, {useContext} from "react";
import {ConfigSearchMaxWidth, Filler, useMobile} from "./Extensions";
import {ProjectMap, projectThumbnailUrl} from "../data/ProjectData";
import {useRadiantContext} from "./RadiantContext";
import {ROUTES} from "../RadiantRouter";

const ConfigBackgroundColor = "#202e37";

/** A tiny project card that fits in the search suggestions */
export const ProjectCardite = (props: {projectId: string}) => {
  const theme = useTheme();
  const context = useRadiantContext();
  const thumbnailSize = "100px";
  const gradientAngle = "120deg";
  const gradientStart = "50px";
  const gradientEnd = "100px";
  const hoverColor = theme.palette.common.white;

  const project = ProjectMap[props.projectId];

  return (
    <Card sx={{
      maxWidth: ConfigSearchMaxWidth - 2,
      overflow: "hidden",
      boxShadow: theme.shadows[5],

      backgroundColor: ConfigBackgroundColor,
      borderStyle: "solid",
      borderWidth: 1,
      borderRadius: 0,
      borderColor: alpha(theme.palette.common.white, 0.15),
    }}>
      <ButtonBase
        onClick={() => {
          context.setSuggestionsOpen(false);
          context.navigate(ROUTES.Library + "/" + project.id);
        }}
        sx={{width: "100%", maxWidth: ConfigSearchMaxWidth}}
      >
        <Box sx={{
          width: "100%",
          maxWidth: ConfigSearchMaxWidth,
          backgroundImage: `url(${projectThumbnailUrl(project)})`,
          backgroundPositionX: "left",
          backgroundPositionY: "top",
          backgroundRepeat: "no-repeat",
          backgroundSize: "auto 100%",
        }}>
          <Stack sx={{
            backgroundImage: `linear-gradient(${gradientAngle}, ${alpha(ConfigBackgroundColor, 0)} ${gradientStart}, ${alpha(ConfigBackgroundColor, 1)} ${gradientEnd})`,
          }}>
            <Stack sx={{
              paddingLeft: thumbnailSize,
              paddingRight: theme.spacing(2),
              paddingY: theme.spacing(1),
              backgroundColor: alpha(hoverColor, 0),
              "&:hover": {backgroundColor: alpha(hoverColor, 0.10)},
              transition: "background-color 100ms linear",
            }}>
              <Typography align="left" noWrap>{project.game}</Typography>
              <Typography align="left" noWrap>{project.song}</Typography>
            </Stack>
          </Stack>
        </Box>
      </ButtonBase>
    </Card>
  );
};

/** Displays text that more projects were found */
export const SummaryProjectCardite = (props: {extraCount: number, clearCode: () => void}) => {
  const theme = useTheme();
  const context = useRadiantContext();
  const projectsFound = React.useMemo(() => context.foundProjects.length > 0, [context]);
  const projectsEmpty = React.useMemo(() => context.foundProjects.length == 0, [context]);
  const isMobile = useMobile();

  return (
    <Card sx={{
      maxWidth: ConfigSearchMaxWidth - 2,
      overflow: "hidden",
      boxShadow: theme.shadows[5],

      backgroundColor: ConfigBackgroundColor,
      borderStyle: "solid",
      borderWidth: 1,
      borderRadius: 0,
      borderColor: alpha(theme.palette.common.white, 0.15),
    }}>
      <Box sx={{
        width: "100%",
        maxWidth: ConfigSearchMaxWidth,
      }}>
        <Stack direction="row" spacing={1} padding={1}>
          {projectsFound && <Typography paddingLeft={1} align="left" noWrap>And {Math.max(0, props.extraCount)} more...</Typography>}
          {projectsEmpty && <Typography paddingLeft={1} align="left" noWrap>No songs found...</Typography>}
          <Filler />
          <Button variant="outlined" size="small" onClick={() => props.clearCode()}>{isMobile ? "Clear" : "Clear Search"}</Button>
          {projectsFound && <Button variant="contained" size="small">Show All</Button>}
        </Stack>
      </Box>
    </Card>
  );
};
