import {alpha, Box, ButtonBase, Card, Stack, Typography, useTheme} from "@mui/material";
import React from "react";
import {ConfigProjectCardWidth, Filler, Spacer, useMobile} from "./Extensions";
import {ProjectMap, projectThumbnailUrl} from "../data/ProjectData";
import moment from "moment";
import {useRadiantContext} from "./RadiantContext";
import {ROUTES} from "../RadiantRouter";

/** A centered box with optional background that keeps content an expected width */
export const ProjectCard = (props: {projectId: string}) => {
  const theme = useTheme();
  const isMobile = useMobile();
  const context = useRadiantContext();

  const project = ProjectMap[props.projectId];

  const difficultyColor = React.useMemo(() => {
    switch (project.difficulty) {
      case "easy":
        return "#75a743";
      case "medium":
        return "#be772b";
      case "hard":
        return "#a53030";
      default:
        return "#4f8fba";
    }
  }, [props]);

  return (
    <Card sx={{
      minWidth: isMobile ? "100%" : ConfigProjectCardWidth,
      maxWidth: isMobile ? "100%" : ConfigProjectCardWidth,
      overflow: "hidden",

      boxShadow: theme.shadows[5],
      backgroundColor: "#151d28",
      borderStyle: "solid",
      borderWidth: 1,
      borderColor: alpha(theme.palette.common.white, 0.15),
      "&:hover": {backgroundColor: "#202e37"},
      transition: "background-color 100ms linear",
    }}>
      <ButtonBase onClick={() => context.navigate(ROUTES.Library + "/" + project.id)}>
        <Stack spacing={isMobile ? 2 : 3} sx={{maxWidth: ConfigProjectCardWidth}}>

          <Spacer size={0} />
          <Stack sx={{paddingX: theme.spacing(isMobile ? 2 : 3)}}>
            <Typography align="left" textTransform="uppercase" noWrap>{project.game}</Typography>
            <Typography align="left" variant="h2" noWrap>{project.song}</Typography>
            <Stack direction="row" spacing={1} sx={{textOverflow: "ellipsis"}}>
              <Typography align="left" noWrap textTransform="capitalize" sx={{color: difficultyColor}}>{project.difficulty}</Typography>
              <Filler />
              <Typography align="right" noWrap textTransform="capitalize" sx={{color: "#a8b5b2"}}>{moment(project.releaseTimestamp).fromNow()}</Typography>
            </Stack>
          </Stack>
          <Box component="img" sx={{width: isMobile ? "100%" : ConfigProjectCardWidth - 2}} src={projectThumbnailUrl(project)} />

        </Stack>
      </ButtonBase>
    </Card>
  );
};
